import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {VsanHealthMutationProvider} from "@generated/vsan-health-mutation-provider";
import {VsanConvertDiskFormatSpec} from "@generated/vsan-convert-disk-format-spec";
import {RemediateClusterConfigContextData} from "./remediate-cluster-config-context.data";

/**
 * This is the dialog component for:
 * 1. remediate the cluster configuration
 * 2. remediate dedup service
 * 3. update esxi configuration
 */
@Component({
   templateUrl: "remediate-cluster-config.html",
   styleUrls: ["remediate-cluster-config.scss"],
   selector: "vsan-health-remediate-cluster"
})
export class RemediateClusterConfigDialogComponent implements OnInit {

   title: string;
   isDedupAction: boolean;
   allowReducedRedundancy: boolean = false;
   description: string;
   alertMessages: string[];

   constructor(private mutationProvider: VsanHealthMutationProvider) {
   }

   public ngOnInit(): void {
      let context: RemediateClusterConfigContextData = VsanUiUtils.getModalContext();
      if (context) {
         this.title = context.title;
         this.isDedupAction = context.isDedupAction;
      }
      this.description = this.isDedupAction ?
            VsanUiUtils.getString("vsan.monitor.health.disk.format.udpate.desc") :
            VsanUiUtils.getString("vsan.monitor.health.remediate.cluster.config.dlg.desc");
   }

   ok = (): void => {
      if (this.isDedupAction) {
         this.mutationProvider.performUpgrade(ManagedObject.contextObject,
               VsanConvertDiskFormatSpec.Factory.create(
                     ManagedObject.contextObject,
                     this.allowReducedRedundancy))
               .then(this.close)
               .catch(error => this.alertMessages = [error]);
      } else {
         this.mutationProvider.remediateCluster(ManagedObject.contextObject)
               .then(this.close)
               .catch(error => this.alertMessages = [error]);
      }
   };

   close = (task: ManagedObject): void => {
      VsanUiUtils.closeModalDialog(task);
   }
}