import {Component} from "@angular/core";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {ManagedObject} from "@service/managed-object";
import {AskVmwareId} from '@service/navigation/model/ask-vmware-id';
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   selector: "vsan-remediate-file-service-imbalance",
   templateUrl: "remediate-file-service-imbalance.html",
   styleUrls: ["remediate-file-service-imbalance.scss"],
})
export class RemediateFileServiceImbalanceComponent {
   readonly AskVmwareId: typeof AskVmwareId = AskVmwareId;

   alertMessages: string[];

   constructor(private fileServiceConfigService: VsanFileServiceConfigService) {}

   ok = () => {
      this.fileServiceConfigService.rebalanceFileService(ManagedObject.contextObject)
            .then(this.close)
            .catch(err => this.alertMessages = [err]);
   };

   close = (task: ManagedObject): void => {
      VsanUiUtils.closeModalDialog(task);
   };
}
