import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {VumLoginService} from "@generated/vum-login-service";
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {ReactiveFormUtil} from "@util/reactive-form-util";

/**
 * This is the dialog for login to my.vmware.com action
 */
@Component({
   templateUrl: "vum-login-action.html",
   styleUrls: ["vum-login-action.scss"],
   selector: "vsan-login-vum-depot"
})
export class VumLoginDialogComponent implements OnInit {

   readonly CONTROL_NAME: string = "name";
   readonly CONTROL_PASSWORD: string = "password";
   readonly INVALID_USERNAME: string = "invalidUsername";
   readonly INVALID_PASSWORD: string = "invalidPassword";

   errorMessages: string[];
   clusterRef: ManagedObject = ManagedObject.contextObject;
   loading: boolean;

   usernameError: string;
   passwordError: string;

   form: FormGroup;

   constructor(private loginService: VumLoginService, private formBuilder: FormBuilder) {
   }

   ngOnInit() {
      const formGroupConfig: any = {};
      formGroupConfig[this.CONTROL_NAME] = new FormControl("", Validators.required);
      formGroupConfig[this.CONTROL_PASSWORD] = new FormControl("", Validators.required);
      this.form = this.formBuilder.group(formGroupConfig);
   }

   ok = () => {
      if (!this.form.valid) {
         if (!this.form.touched) {
            ReactiveFormUtil.validateAllFormFields(this.form);
         }

         return;
      }

      this.loading = true;
      const username: string = this.form.get(this.CONTROL_NAME).value.trim();
      const password: string = this.form.get(this.CONTROL_PASSWORD).value.trim();
      this.loginService.loginToVum(this.clusterRef, username, password)
            .then(() => {
               this.loading = false;
               //just close the dialog
               this.close();
            })
            .catch((error) => {
               this.loading = false;
               this.errorMessages = [error];
            });
   };

   close = () => {
      this.errorMessages = null;
      VsanUiUtils.closeModalDialog();
   };
}