import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {RestoreHealthSettings} from "./model/restore-health-settings.data";
import {RestoreHealthActionContext} from "./model/restore-health-action-context.data";

@Component({
   selector: "vsan-restore-health-action",
   templateUrl: "restore-health-action.html",
   styleUrls: ["restore-health-action.scss"],
})
export class RestoreHealthActionComponent implements OnInit {
   runRetest: boolean = true;
   private isOnlineTest: boolean;

   ngOnInit(): void {
      const context: RestoreHealthActionContext = VsanUiUtils.getModalContext();
      this.isOnlineTest = context.isOnlineTest;
   }

   confirm(): void {
      VsanUiUtils.closeModalDialog(new RestoreHealthSettings(this.runRetest, this.isOnlineTest));
   }
}
