import {NgModule} from "@angular/core";
import {ClarityModule} from "@clr/angular";
import {CommonModule} from "@angular/common";
import {RouterModule, Routes} from "@angular/router";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {CommonPipeModule} from "@pipe/common-pipe.module";
import {PromptModule} from "@component/dialog/prompt/prompt.module";
import {ValidationModule} from "@component/validation/validation.module";
import {VumLoginDialogComponent} from "./vum-login-action.component";
import {NotSupportedActionComponent} from "./not-supported-action.component";
import {RelayoutVsanObjectsComponent} from "./relayout/relayout-objects.component";
import {MarkSilentHealthActionComponent} from "./mark-silent-health-action.component";
import {PurgeInaccessibleSwapObjectsActionComponent} from "./purge-inaccessible-objects/purge-inaccessible-swap-objects-action.component";
import {CommonDirectiveModule} from "@directive/common-directive.module";
import {RestoreHealthActionComponent} from "./restore-health-action.component";
import {RemediateClusterConfigDialogComponent} from "./remediate-cluster-config.component";
import {RemediateFileServiceImbalanceComponent} from "./remediate-file-service-imbalance.component";
import {ProactiveRebalanceActionComponent} from "./proactive-rebalance-action.component";

const healthActionsRoutes: Routes = [
   {
      path: "loginVumDepot",
      component: VumLoginDialogComponent,
   },
   {
      path: "notSupportedAction",
      component: NotSupportedActionComponent,
   },
   {
      path: "relayoutVsanObjects",
      component: RelayoutVsanObjectsComponent,
   },
   {
      path: "markSilent",
      component: MarkSilentHealthActionComponent,
   },
   {
      path: 'restoreAlert',
      component: RestoreHealthActionComponent,
   },
   {
      path: "remediateCluster",
      component: RemediateClusterConfigDialogComponent,
   },
   {
      path: "purgeInaccessibleSwapObjs",
      component: PurgeInaccessibleSwapObjectsActionComponent,
   },
   {
      path: "remediateFileServiceImbalance",
      component: RemediateFileServiceImbalanceComponent,
   },
];

@NgModule({
   imports: [
      FormsModule,
      PromptModule,
      CommonModule,
      ClarityModule,
      ValidationModule,
      CommonDirectiveModule,
      CommonPipeModule,
      ReactiveFormsModule,
      RouterModule.forChild(healthActionsRoutes),
   ],
   declarations: [
      VumLoginDialogComponent,
      NotSupportedActionComponent,
      RelayoutVsanObjectsComponent,
      MarkSilentHealthActionComponent,
      RestoreHealthActionComponent,
      ProactiveRebalanceActionComponent,
      RemediateClusterConfigDialogComponent,
      PurgeInaccessibleSwapObjectsActionComponent,
      RemediateFileServiceImbalanceComponent,
   ],
   exports: [
      ProactiveRebalanceActionComponent,
   ],
})
export class HealthActionsModule {
}
