import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {Logger} from "@util/logger";
import {PurgeInaccessibleVmSwapObjectsProvider} from "@generated/purge-inaccessible-vm-swap-objects-provider";

@Component({
   templateUrl: "purge-inaccessible-swap-objects-action.html",
   selector: "vsan-purge-inaccessible-swap-objects-action",
   styleUrls: ["purge-inacessible-swap-objects-action.scss"]
})
export class PurgeInaccessibleSwapObjectsActionComponent implements OnInit {

   constructor(private purgeSwapObjectsProvider: PurgeInaccessibleVmSwapObjectsProvider) {}

   objectUuids: string[] = [];
   errorMessages: string[] = [];
   loading: boolean = true;


   ngOnInit(): void {
      this.purgeSwapObjectsProvider.getInaccessibleVmSwapObjects(ManagedObject.contextObject)
            .then((uuids: string[]) => {
               this.objectUuids = uuids;
               if (this.objectUuids.length > 0) {
                  // Add some space in order to fit the warning box that appears in this case
                  VsanUiUtils.setModalDialogHeight(260);
               }
               this.loading = false;
            }).catch((error) => {
               Logger.error("Unable to query vsan inaccessible VM swap objects: ", error);
               this.errorMessages.push(error);
               this.loading = false;
            });
   }

   purgeSwapObjects = (): void => {
      this.errorMessages = [];
      this.purgeSwapObjectsProvider.purgeInaccessibleVmSwapObjects(ManagedObject.contextObject, this.objectUuids)
            .then((task: ManagedObject) => {
               VsanUiUtils.closeModalDialog(task);
            }).catch(error => {
               Logger.error("Unable to start purge inaccessible objects task", error);
               this.errorMessages = [VsanUiUtils.getString("vsan.common.error.taskError")];
            });
   }
}
