import {Component} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {VsanHealthPropertyProvider} from "@generated/vsan-health-property-provider";

@Component({
   selector: "vsan-mark-silent-action",
   templateUrl: "mark-silent-health-action.html",
   styleUrls: ["./mark-silent-health-action.scss"],
})
export class MarkSilentHealthActionComponent {
   alertMessages: string[];

   constructor(private healthPropertyProvider: VsanHealthPropertyProvider) {}

   confirm = (): void => {
      const clusterRef: ManagedObject = ManagedObject.contextObject;
      const context: SilentHealthActionContext = VsanUiUtils.getModalContext();
      this.healthPropertyProvider.setSilentChecks(clusterRef, context.isVsphereHealth, [context.testId], [])
            .then(() => {
               VsanUiUtils.closeModalDialog(true);
            })
            .catch((err) => {
               this.alertMessages = [VsanUiUtils.getString("vsan.monitor.health.silent.error")];
            });
   };
}

export interface SilentHealthActionContext {
   testId: string;
   isVsphereHealth: boolean;
}