import {Component} from "@angular/core";
import {RelayoutVsanObjectsProvider} from "@generated/relayout-vsan-objects-provider";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "relayout-objects-action.html",
   selector: "vsan-relayout-objects-action",
   styleUrls: ["relayout-objects-action.scss"]
})
export class RelayoutVsanObjectsComponent {

   errorMessages: string[];
   loading: boolean = false;

   constructor(private relayoutService: RelayoutVsanObjectsProvider) {}

   async relayoutObjects() {
      this.errorMessages = null;
      try {
         this.loading = true;
         const task:ManagedObject = await this.relayoutService.relayoutObjects(ManagedObject.contextObject);
         VsanUiUtils.closeModalDialog(task);
      } catch (e) {
         this.errorMessages = [e];
      } finally {
         this.loading = false;
      }
   }
}